const EmojiPicker = ({ applyStyle }) => (
  <Dropdown title="Insert Emoji" icon={<Smile size={16} />}>
    <div className="p-2 d-flex flex-wrap gap-1" style={{ maxHeight: "200px", width: "250px", overflowY: "auto" }}>
      {EMOJIS.map((emoji) => (
        <button key={emoji} className="btn btn-sm btn-light fs-5" onClick={() => applyStyle("insertText", emoji)}>{emoji}</button>
      ))}
    </div>
  </Dropdown>
);
EmojiPicker.propTypes = {applyStyle: PropTypes.func.isRequired,};
export default EmojiPicker;